@charset "UTF-8";

.contents {
  position: relative;
}

body {
  margin: 0;
  overflow: hidden;
}

html, body, .contents {
  height: 100%;
}

textarea:focus, input:focus {
  outline: none;
}

*:focus {
  outline: none;
}

#left-menu {
  width: 17%;
  height: 100%;
  background: #222626;
  z-index: 5;
}

#left-menu .top {
  height: 6%;
  position: relative;
}

#left-menu .top .img-wrap {
  position: absolute;
  top: 10px;
  left: 18px;
}

#left-menu .top .img-wrap #left-menu-logo {
  width: 36px;
  height: 30px;
}

#left-menu .top #toggle-menu {
  width: 28px;
  position: absolute;
  top: 15px;
  right: 18px;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
}

#left-menu .top #toggle-menu span {
  display: block;
  width: 100%;
  height: 1px;
  background: #9ea5a5;
  margin-bottom: 6px;
  border-radius: 3px;
}

#left-menu .top:after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 24px;
  margin: 0 auto;
  width: 83%;
  border-bottom: 1px solid #414444;
}

#left-menu #profile {
  text-align: center;
  padding-top: 29%;
}

#left-menu #profile .img-wrap #profile-img {
  width: 100px;
  height: 100px;
  cursor: pointer;
}

#left-menu #profile .text-box .p-text-large {
  color: #fff;
  font-size: 16px;
}

#left-menu #profile .text-box .p-text-small {
  color: #fff;
  font-size: 12px;
}

#left-menu #left-menu-item {
  margin-top: 20%;
}

#left-menu #left-menu-item .item-wrap {
  cursor: pointer;
  position: relative;
}

#left-menu #left-menu-item .item-wrap .img-wrap {
  position: absolute;
  left: 24%;
}

#left-menu #left-menu-item .item-wrap p {
  margin-left: 39%;
  margin-bottom: 10%;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 27px;
  /* identical to box height */
  letter-spacing: 0.03em;
  color: #9EA5A5;
}

#left-menu #left-menu-item .item-wrap .current {
  color: #7ad2cc;
}

#left-menu #left-menu-item .item-wrap p:hover {
  color: #7ad2cc;
}

#left-menu-after {
  display: none;
  width: 6%;
  height: 100%;
  background: #fff;
  z-index: 5;
}

#left-menu-after .top {
  height: 6%;
  position: relative;
}

#left-menu-after .top #toggle-menu-after {
  width: 28px;
  position: absolute;
  top: 32%;
  left: 32%;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
}

#left-menu-after .top #toggle-menu-after span {
  display: block;
  width: 100%;
  height: 1px;
  background: #000;
  margin-bottom: 6px;
  border-radius: 3px;
}

#left-menu-after .top:after {
  content: "";
  position: absolute;
  bottom: -8px;
  margin: 0 auto;
  width: 100%;
  border-bottom: 1px solid #ebeded;
}

#left-menu-after #profile {
  text-align: center;
  padding-top: 53%;
}

#left-menu-after #profile .img-wrap #profile-img {
  width: 40px;
  height: 40px;
  cursor: pointer;
}

#left-menu-after #left-menu-item {
  margin-top: 143px;
}

#left-menu-after #left-menu-item .item-wrap {
  cursor: pointer;
}

