#voting-modals #mask {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 99;
  opacity: 0;
  visibility: hidden;
}

#voting-modals .modal {
  position: fixed;
  top: 6%;
  left: 34%;
  width: 600px;
  height: 754px;
  background: #fff;
  box-shadow: 0px 0px 20px rgba(50, 82, 82, 0.5);
  border-radius: 10px;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: .6s;
}

#voting-modals .visible {
  opacity: 1 !important;
  visibility: visible !important;
}

#voting-modals #new-first-modal .inner-modal .top-text {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
